import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import { Text, Localizer, withText } from 'preact-i18n';
import Markdown from 'preact-markdown';

import Modal from '../modal';
import WorkoutComments from '../../components/workout/comments';
import WorkoutAddComment from '../../components/workout/commentAdd';
import ImageScroller from '../../components/imageScroller';
import DogScroller from '../../components/dog/scroller';

import Overlay from '../../components/overlay/';
import TeamsPublicDetail from '../../routes/teams/publicDetail';
import DogPublicDetail from '../../routes/dogs/publicDetail';

import LikesAndComments from '../../components/feedback/likesAndComments';
import Views from '../../components/feedback/views';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

const MAX_LOADINDEX = mu.getMaxLoadIndex();

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		case 4:
			return 'border-info';
		default:
			return 'border-warning';
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		case 4:
			return 'fas fa-futbol';
		default:
			return 'fas fa-hiking';
	}
}

function displayDistance(distanceKm) {
	if (distanceKm < 5) {
		return util.format(distanceKm, 2);
	}
	if (distanceKm < 10) {
		return util.format(distanceKm, 1);
	}
	return util.format(distanceKm, 1);
}

const initialState = {
	showAddComment: false,
    viewImageIdx: {},
    likeWorkout: {},
};

@observer
class Workout extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

    toggleDate = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const { showDate } = this.state;
        this.setState({ showDate: !showDate });
    }


	likeWorkout = async () => {
		const { workoutStore } = this.props.stores;
        const { workout } = this.props;
        const workoutId = workout.id;

        const { likeWorkout } = this.state;
        likeWorkout[workoutId] = true;
        this.setState({ likeWorkout });
        setTimeout(() => {
            const { likeWorkout } = this.state;
            delete likeWorkout[workoutId];
            this.setState({ likeWorkout });
        }, 1200);

		const response = await workoutStore.likeWorkout({ id: workoutId });
        if (response && response.status === 200) {

		}
	}

	toggleAddComment = (e) => {
		const workoutId = e.target.parentElement.dataset.id || e.target.dataset.id;
		const { showAddComment } = this.state;
		this.setState({
			showAddComment: !showAddComment,
		}, () => {
			if (this.inputRef[workoutId]) {
				this.inputRef[workoutId].focus();
			}
		});
	}

    viewImage = (e) => {
		const { id, idx } = e.target.closest('img').dataset;
        const { viewImageIdx } = this.state;
        viewImageIdx[id] = idx;
        this.setState({ viewImageIdx });
    }

    shareLink = (e) => {
        const { url, title, text } = e.target.closest('button').dataset;
        const { appState } = this.props.stores;
        appState.shareUrl({ url, title, text });
    }

        // Overlay functions
    onTeamClick = (e, teamid) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showTeam: teamid,
        });
        return false;
    }

    hideTeam = () => {
        this.setState({
            showTeam: null,
        });
    }

    onUserClick = (e, userid) => {
        e.preventDefault();
        e.stopPropagation();
        return false;
    }

    onDogClick = (e, dogid, dogs) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showDog: dogid,
            dogs,
        });
        return false;
    }

    hideDog = () => {
        this.setState({
            showDog: null,
        });
    }

    render() {
        const {
            showDate,
            showAddComment,
            viewImageIdx = {},
            likeWorkout,
            dogs,
            showTeam,
            showDog,
        } = this.state;
        const {
            workout,
            showDate: forceShowDate,
            wrapperClasses = 'col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3',
            wrapperClassesRow = 'row',
        } = this.props;
		const { userStore, appState, workoutStore } = this.props.stores;
        const { viewmode, hasShareApi } = appState;
        const { user, dogs: allDogs } = userStore;
        const { language = 'en' } = user;
        const intensity = userStore.findIntensity({ code: workout.intensity, language }) || {};
        // const teamName = userStore.findTeam(workout.team);
        const team = userStore.findTeam(workout.team, true) || {};
        const teamName = team.name;
        // const teamNames = workout.teams ? workout.teams.map(e => userStore.findTeam(e)) : [];
        const teamNames = [];
        if (teamName) {
            teamNames.push(teamName);
        }
        if (workout.teams) {
            workout.teams.forEach((e) => {
                if (userStore.findTeam(e)) {
                    teamNames.push(userStore.findTeam(e));
                }
            });
        }

        const { likes = [], comments = [] } = workout;
		const hasLikedWorkout = workout.likes && workout.likes.findIndex(e => e.user === user.id) > -1;
        const { gpxInfo = {}, mushers = [] } = workout;
		const { loadIndex = 0 } = gpxInfo;
        const workoutEquipment = userStore.findEquipment({ equipment: workout.equipment, language });
        const workoutEquipmentIcon = userStore.findEquipment({ equipment: workout.equipment, field: 'icon' });

        const musherImages = [];
        if (mushers.length > 1) {
            mushers.slice(1, 10).map((musher) => {
                const u = workoutStore.findPublicUser(musher) || {};
                if (u.images && u.images[0]) {
                    musherImages.push(u.images[0]);
                }
            });
        }

        const publicUser = workoutStore.findPublicUser(mushers[0]) || {};
        let image;
        if (publicUser && publicUser.images && publicUser.images[0]) {
            image = publicUser.images[0];
        } else if (team.image && team.image.s3SmallLink) {
            image = team.image;
        } else if (team.images && team.images[0] && team.images[0].s3SmallLink) {
            image = team.images[0];
        } else if (workout.image && workout.image.s3SmallLink) {
            image = workout.image;
        }

        const images = [];
        if (workout.mapImage) {
            images.push({
                ...workout.mapImage,
                type: 'map',
            });
        }
        if (workout.images) {
            const imgs = toJS(workout.images);
            images.push(...imgs);
        }
        const imageIdx = viewImageIdx[workout.id] ? viewImageIdx[workout.id] : 0;
        const currentImage = images && images[imageIdx] ? images[imageIdx] : null;

        let batteryUsage;
        if (workout && workout.gpxInfo && workout.gpxInfo.battegryLevelStart) {
            batteryUsage = (workout.gpxInfo.battegryLevelStart - workout.gpxInfo.battegryLevelEnd) * 100;
        }

        const views = mu.getViews(workout.statistics);

        // let temperature;
        // let windSpeed;
        // if (util.checkNested(workout, 'temperature')) {
        //     const yr = toJS(workout.yr[0].instant.details);
        //     // displayValue = `Yr.no: 🌡️ ${yr.air_temperature}°C, 💨 ${yr.wind_speed}m/s 🧭 ${mu.windDirection(yr.wind_from_direction)}, ☁️ ${yr.cloud_area_fraction}%, ${yr.air_pressure_at_sea_level}mb, 💦 ${yr.relative_humidity}%`;
        //     temperature = `${yr.air_temperature}°C`;
        //     windSpeed = `${yr.wind_speed}m/s`;
        // }
		// // const loadMultiplier = loadIndex / 100;

        return (<>

            {showTeam && <Overlay>
                <TeamsPublicDetail stores={this.props.stores} teamid={showTeam} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideTeam} hideTeam={this.hideTeam} />
            </Overlay>}

            {showDog && <Overlay>
                <DogPublicDetail stores={this.props.stores} dogid={showDog} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideDog} dogs={dogs} />
            </Overlay>}

            <div class='w-100 mb-2'>
                <div class={wrapperClassesRow}>

                    <div class={`${wrapperClasses} d-flex mb-0 position-relative`}>

                        {/* {workout.gpxInfo && <svg viewBox='0 0 300 300' style='top: 80px; left: 10px; width: 90px; height: 90px;' class='position-absolute'>
                            <path d={`${workout.gpxInfo.svgMap}`} style='fill: none; stroke: #0063FF; stroke-width: 5; opacity: 0.3;' />
                        </svg>} */}

                        {/* {JSON.stringify(workout, null, 4)} */}
                        <div class='text-center'>
                            <div
                                class={`position-relative text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`}
                                style={`${image ? `background-image: url("${image.s3SmallLink}"); background-size: cover;` : ''} border-width: 3px !important;`}
                            >
                                {!image && <i class={`${getWorkoutIcon(workout)} text-${intensity.class ? intensity.class : 'muted'} mt-3`} style='font-size: 40px;' />}

                                {musherImages && musherImages.map((img, idx) => <>
                                    <div
                                        class='position-absolute rounded-circle imageRounded border imageRoundedThumb border-white'
                                        style={`
                                            background-image: url("${img.s3SmallLink}");
                                            background-size: cover;
                                            bottom: -3px;
                                            left: ${-5 + (idx * 10)}px;
                                        `}
                                    />
                                </>)}
                            </div>
                            {workout.temperature !== 0 && <>
                                <span class='text-muted font-weight-light'>
                                    <Temperature stores={this.props.stores} value={workout.temperature} />
                                </span>
                            </>}
                        </div>
                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                            <span class='float-right'>
                                {workout.isLocked === 1 && <i class='fas fa-lock text-danger ml-3' />}
                                {workout.isInProgress === 1 && <div class='spinner-grow text-success ml-3' role='status' />}
                                {workout.isInProgress === 2 && <i class='fas fa-pause-circle text-warning ml-3' />}
                            </span>
                            <a class='stretched-link' href={`/workouts/${workout.id}`}>
                                <h5 class='my-0 mb-1' style='line-height: 1.0em;'>
                                    {workout.name}
                                </h5>
                            </a>
                            {teamNames && teamNames.length > 0 && <small class='font-weight-lighter'>
                                {teamNames.map((name, idx) => <>{idx > 0 ? ', ' : ''}{name}</>)}
                            </small>}
                            <br />
                            <small class='text-muted'>
                                {workout.date && <small onClick={this.toggleDate}>
                                    {forceShowDate || showDate ? util.formatDate(workout.date, { locale: 'no-NB', hour12: false, hour: '2-digit', minute: '2-digit' }) : util.formatDistance(workout.date, new Date(), { locale: 'no-NB' })}
                                </small>}
                                <span class={`badge badge-${intensity.class} ml-2`}>{intensity.name}</span>
                                {loadIndex > 0 && <span class={`ml-2 text-${mu.getLoadIndexColor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}`}>
                                    <i class='fas fa-hand-rock mr-1' />
                                    {Math.floor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))} -&nbsp;
                                    {mu.getLoadIndexText(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                </span>}
                                <br />
                            </small>
                            <small>
                                <div class='text-muted'>
                                    {workout.garminId > 0 && <div class={`float-left mr-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url("./assets/garmin-connect.png");`} />}
                                    {workout.stravaId > 0&& <div class={`float-left mr-2 text-center border rounded-circle imageRounded imageRoundedIcon`} style={`background-image: url("./assets/strava-icon.png");`} />}

                                    {workout.equipment > 0 && <><i class={`${workoutEquipmentIcon} ml-2`} /> {workoutEquipment}</>}
                                    {/* {workout.distanceKm > 0 && <><i class='fas fa-road ml-2' /> {displayDistance(workout.distanceKm)}km</>}
                                    {workout.elevation > 0 && <><i class='fas fa-mountain ml-2' /> {util.format(workout.elevation, 0)}m</>} */}
                                    {/* {workout.speedAvg > 0 && <><i class='fas fa-tachometer-alt ml-2' /> {util.format(workout.speedAvg, 1)}km/t</>} */}
                                    {workout.dogs > 0 && <><i class='fas fa-dog ml-2' /> {workout.dogs.length}</>}
                                    {workout.track > 0 && <><i class={`fas fa-map-marked text-${workout.track > 0 ? 'success' : 'danger'} ml-2`} /></>}
                                    {workout.dogsLeft > 0 && <><i class={`fas fa-crosshairs text-${workout.dogsLeft && workout.dogsRight ? 'success' : 'danger'} ml-2`} /></>}
                                    {workout.sledWeight > 0 && <>
                                        <i class='fas fa-sleigh ml-2' /> <Weight stores={this.props.stores} value={workout.sledWeight} />
                                    </>}
                                    {batteryUsage > 0 && <span class='text-muted ml-2'><i class='fas fa-battery-three-quarters' /> {util.format(batteryUsage, 1)}% <Text id='workout.battery-used'>used</Text></span>}
                                </div>
                                {['beta', 'advanced'].indexOf(viewmode) > -1 && (
                                    <span class='text-muted'>
                                        <i class='fas fa-balance-scale' /> {util.format(workout.kcalBurnedTotal)} <Text id='workout.kcal'>kcal</Text>
                                        <i class='fas fa-drumstick-bite ml-2' /> <Weight stores={this.props.stores} value={workout.kcalMeatGramsTotal / 1000} />
                                        <i class='fas fa-fish ml-2' /> <Weight stores={this.props.stores} value={workout.kcalSalmonGramsTotal / 1000} />
                                        <i class='fas fa-piggy-bank ml-2' /> <Weight stores={this.props.stores} value={workout.kcalFatGramsTotal / 1000} />
                                    </span>
                                )}
                            </small>
                        </div>
                    </div>
                    <div class={`${wrapperClasses} position-relative mt-2`}>
                        <strong>
                            {workout.description}
                        </strong>
                    </div>
                    <div class={`${wrapperClasses} position-relative`}>
                        <Markdown markdown={mu.replaceImages(workout.comment, workout.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                    </div>
                    <div class={`${wrapperClasses} d-flex mb-0 position-relative`}>
                        <div class='container-fluid px-0'>
                            <div class='row mt-1'>
                                {workout.distanceKm > 0 && <div class='col-3'>
                                    <div class='text-overflow'>
                                        <small><i class='fas fa-road' /> <Text id='workout.calendar.distance'>Distanse</Text></small>
                                    </div>
                                    <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                        <Distance stores={this.props.stores} value={workout.distanceKm} />
                                    </div>
                                </div>}
                                {workout.elevation > 0 && <div class='col-3'>
                                    <div class='text-overflow'>
                                        <small><i class='fas fa-mountain' /> <Text id='workout.calendar.ascend'>Høydemeter</Text></small>
                                    </div>
                                    <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                        <Elevation stores={this.props.stores} value={workout.elevation} />
                                    </div>
                                </div>}
                                {workout.duration > 0 && <div class='col-3'>
                                    <div class='text-overflow'>
                                        <small><i class='fas fa-clock' /> <Text id='workout.calendar.duration'>Varighet</Text></small>
                                    </div>
                                    <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
                                        {util.secToHms(workout.duration, true)}
                                    </div>
                                </div>}
                                {workout.speedAvg > 0 && <div class='col-3'>
                                    <div class='text-overflow'>
                                        <small><i class='fas fa-tachometer-alt' /> <Text id='workout.calendar.avg-speed'>Snittfart</Text></small>
                                    </div>
                                    <div class='text-overflow font-weight-light' style='font-size: 1.2em;'>
				                        {workout.gpxInfo && workout.gpxInfo.avgGpsMovingSpeed > 0 ? <>
                                            <Speed stores={this.props.stores} value={workout.gpxInfo.avgGpsMovingSpeed * 3.6} />
                                        </> : <>
                                            <Speed stores={this.props.stores} value={workout.speedAvg} />
                                        </>}
                                    </div>
                                </div>}
                            </div>
                        </div>
                    </div>

                    {['beta', 'advanced', 'normal'].indexOf(viewmode) > -1 && (
                        <div class={`${wrapperClasses}`}>
                            <div class='container-fluid px-0 mt-1'>
                                <div class='row'>
                                    <ImageScroller stores={this.props.stores} images={images} id={workout.id} dblClick={this.likeWorkout} likeRef={likeWorkout} />
                                </div>
                            </div>
                        </div>
                    )}

                    {['beta', 'advanced', 'normal'].indexOf(viewmode) > -1 && <>
                        {workout.dogs && workout.dogs.length > 0 && <div class='container-fluid px-0 mt-1'>
                            <div class='row pt-2'>
                                <DogScroller stores={this.props.stores} dogs={workout.dogs} allDogs={allDogs} onDogClick={this.onDogClick} />
                            </div>
                        </div>}

                        <div class='mt-2'>
                            <LikesAndComments
                                stores={this.props.stores}
                                likes={likes}
                                comments={comments}
                                type='workout'
                                onTeamClick={this.onTeamClick}
                                onUserClick={this.onUserClick}
                            />
                        </div>

                    </>}

                    {['beta', 'advanced', 'normal'].indexOf(viewmode) > -1 && <>
                        <div class={`${wrapperClasses} mb-0 position-relative mt-2`}>
                            <div class='row'>
                                <div class='col text-center'>
                                    <button type='button' class={`btn btn-sm btn-outline-${hasLikedWorkout ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedWorkout ? 'text-primary' : ''}`}
                                        onClick={this.likeWorkout}
                                        // disabled={hasLikedWorkout}
                                    >
                                        <i class={`${hasLikedWorkout ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> <Text id='comments.like'>Lik</Text>
                                    </button>
                                </div>
                                <div class='col text-center'>
                                    <button type='button' class='btn btn-sm btn-outline-secondary btn-lg btn-block'
                                        data-id={workout.id}
                                        onClick={this.toggleAddComment}
                                    >
                                        <i class='fas fa-comment' /> <Text id='comments.comment-add'>Kommentar</Text>
                                    </button>
                                </div>
                                {hasShareApi ? <>
                                    <div class='col text-center'>
                                        <button class='btn btn-sm btn-outline-secondary btn-lg btn-block text-overflow'
                                            onClick={this.shareLink}
                                            data-url={`https://themusher.app/share/workout/${workout.uuidv4}`}
                                            data-title={`title: ${workout.name}`}
                                        >
                                            <i class='fas fa-share' /> <Text id='workout.share'>Del</Text>
                                        </button>
                                    </div>
                                </> : <>
                                    <div class='col text-center'>
                                        <a class='btn btn-sm btn-outline-secondary btn-lg btn-block text-overflow'
                                            href={`https://themusher.app/share/workout/${workout.uuidv4}`}
                                            target='_blank'
                                            rel='noreferrer'
                                            data-native
                                        >
                                            <i class='fas fa-share' /> <Text id='workout.share'>Del</Text>
                                        </a>
                                    </div>
                                </>}
                            </div>
                        </div>

                        <div class={`${wrapperClasses} mt-2 mb-3`}>
                            {showAddComment && <WorkoutAddComment
                                stores={this.props.stores}
                                workout={workout}
                                inputRef={c => this.inputRef[workout.id] = c}
                                inputRefObject={this.inputRef}
                                col='12'
                                offset='0'
                            />}
                            <WorkoutComments stores={this.props.stores} workout={workout} col='10' offset='2' compact={true} />
                        </div>

                        {views > 0 && <>
                            <div class='mt-2'>
                                <Views
                                    stores={this.props.stores}
                                    views={views}
                                    object={workout}
                                    type='workout'
                                    isTeamAdmin={true}
                                    isAdmin={false}
                                    onTeamClick={this.onTeamClick}
                                    onUserClick={this.onUserClick}
                                />
                            </div>
                        </>}

                    </>}

                </div>
            </div>
        </>);
    }
}

export default Workout;
